 body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace
}

.App {
	text-align: left
}

.App-logo {
	-webkit-animation: App-logo-spin 20s linear infinite;
	animation: App-logo-spin 20s linear infinite;
	height: 40vmin;
	pointer-events: none
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: #fff
}

.App-link {
	color: #61dafb
}

@-webkit-keyframes App-logo-spin {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg)
	}
	to {
		-webkit-transform: rotate(1turn);
		transform: rotate(1turn)
	}
}

@keyframes App-logo-spin {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg)
	}
	to {
		-webkit-transform: rotate(1turn);
		transform: rotate(1turn)
	}
}
.invisible{
	display: inline-block;
	height: 0;
	margin: 0;
	padding: 0;
	border: none;
	overflow: hidden;
	position: absolute;
	color: transparent !important;
}
.formDiv{
	border:1px dashed #ccc;
	background-color:#fafafa;
	padding:10px;
	margin-bottom:10px;
	
}
.formDiv + div{
	float:right;
	
}
form {
	display: inline;

}

form div {
	display: inline-block;
	margin-left: 5px;
}

table {
	border-collapse: collapse;
	width: 100%
}

table a{
	color: #0085db;
	text-decoration:none;
}

table th {
background: #990000; /* Old browsers */
background: -moz-linear-gradient(top,  #990000 0%, #660000 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  #990000 0%,#660000 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  #990000 0%,#660000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#990000', endColorstr='#660000',GradientType=0 ); /* IE6-9 */
	color:white;
}
.partyData{
	font-weight:bold;
}
.partyData .democrat{
	color:#00bde3;
}
.partyData .republican{
	color:#ED553E;
}
.partyData .independent{
	color:#00a91c;
}

.pageControls {
	width: 100%;
	text-align: center
}
.pageCounter{
	position: relative;
    top: 18px;
	
}

.nextButton,
.prevButton {
	display: block
}

.prevButton {
	float: left
}

.nextButton {
	float: right
}

table td,
table th {
	white-space: nowrap
}

table td,
table td>div,
table th {
	padding: 10px;
	border: 1px solid #ccc
}

table td>div {
	margin-bottom: 10px;
	height: 10px;
	overflow: hidden
}

table tr:nth-child(2n) {
	background-color: #F2F8FF
}

img.floatRight {
	float: right;
	height:400px;
	-webkit-box-shadow: 0px 0px 5px 6px #ccc;  
    -moz-box-shadow:    0px 0px 5px 6px #ccc;
    box-shadow:         0px 0px 5px 6px #ccc;

	
}

.mainContent{
	margin:20px;
	
	
}
.mainContent *{
	font-family:Open Sans,sans-serif;
	
}
.hidden{
	display:none;
	
}


select {
    border-radius: 5px;
}




.header {
    position: relative;
    -ms-flex-align: center;
    align-items: center;
    height: 70px;
    background: #092541 linear-gradient(180deg,#20355a,#092541) repeat scroll 0 0;
    z-index: 3;
}

.header__logo {
    background-image: url(https://live.house.gov/assets/clerk-logo-approved-white.3306581c100b7bb8c326537b21ef7865.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    height: 38px;
    width: 300px;
    top: 50%;
    transform: translateY(-50%);
    left: 1em;

}

.btn {
	border: none;
	font-family: inherit;
	font-size: inherit;
	color: white;
	background-color: #183052;
	cursor: pointer;
	padding: 10px 30px;
	display: inline-block;
	margin: 10px 0px;
	text-transform: uppercase;
	letter-spacing: 1px;
	font-weight: 700;
	outline: none;
	position: relative;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	transition: all 0.3s;
}

.btn:after {
	content: '';
	position: absolute;
	z-index: -1;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	transition: all 0.3s;
}

.icon-arrow-right:before {
	content: "\2192";
	margin-top:-18px;
	margin-left:-4px;
}

.icon-arrow-left:before {
	content: "\2190";
	margin-top:-18px;
	margin-left:-10px;
}
.disabled:before{
	content: "\20E0";
	margin-top:-16px;
}
.icon-arrow-right.disabled:before{
	margin-left:5px;
	
}
.icon-arrow-left.disabled:before{
	margin-left:0px;
	
}



.btn-4:active {
	border-color: #183052;
	color: #2E5D9E;
}
.btn-4:hover {
	background: #2E5D9E;
}
.btn-4:before {
	position: absolute;
	height: 100%;
	font-size: 125%;
	line-height: 3.5;
	color: #fff;
	-webkit-transition: all 0.3s;
	-moz-transition: all 0.3s;
	transition: all 0.3s;
}
.btn-4.disabled:before {
	color: #ED553E;
}
.btn-4:active:before {
	color: #fff;
}

.btn-4c:before {
	left: 70%;
	opacity: 0;
	top: 0;
}

.btn-4c:hover:before {
	left: 80%;
	opacity: 1;
}

.btn-4d:before {
	left: 30%;
	opacity: 0;
	top: 0;
}

.btn-4d:hover:before {
	left: 10%;
	opacity: 1;
}
.btn.disabled{
	background-color:gray;
	color:#ccc
}

